<template>
  <div>
    <el-card class="box-card">
      <div class="clearfix" slot="header">
        <span>系统通知</span>
      </div>
      <div class="content">
        <el-collapse @change="handleChange" accordion v-model="mid">
          <el-collapse-item :key="index" :name="msg.id" v-for="(msg,index) in sysmsgs">
            <template slot="title">
              <div class="msg_content">
                <div class="neirong">
                  <div
                    style="width: 8px;height: 8px;background-color: #ea0206;border-radius: 8px;"
                    v-show="msg.state===0"
                  ></div>
                </div>
                <span style="width: 600px;text-align: left">{{msg.msgContent.title}}</span>
                <el-tag>{{msg.msgContent.createTime|formatDate}}</el-tag>
              </div>
            </template>
            <div
              style="display: flex;justify-content: flex-start;align-items: center;border-style: solid;border-width: 1px;border-color: #409eff;border-radius: 5px;padding: 4px 0px 4px 8px;box-sizing: border-box;height: 100%"
            >{{msg.msgContent.message}}</div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      mid: -1,
      activeName: '1',
      sysmsgs: ''
    }
  },
  mounted: function () {
    this.initSysMsgs();
  },
  methods: {
    handleChange (newVal) {
      if (newVal === '') {
        return;
      }
      var _this = this;
      this.putRequest('/notice/mark', { id: this.mid }).then(resp => {
        if (resp && resp.status === 200) {
          _this.initSysMsgs();
        }
      })
    },
    initSysMsgs () {
      var _this = this;
      this.getRequest(
        '/notice/all'
      ).then(resp => {
        _this.sysmsgs = resp.data;
        var isDot = false;
        _this.sysmsgs.forEach(msg => {
          if (msg.state === 0) {
            isDot = true;
          }
        })
        _this.$store.commit('toggleNFDot', isDot);
      })
    }
  }
}
</script>

<style>
.content {
  min-height: 750px;
}
.msg_content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.neirong {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
}
</style>


